import React,{Component,propTypes} from 'react';
import ReactDOM from 'react-dom';
var $  = require( 'jquery' );
var DataTable = require( 'datatables.net' );
require( 'datatables.net-dt/css/jquery.dataTables.css' );
class App extends Component{
	constructor(props){
		super(props);
		this.state={
			data:[],
			$datatable:{},
			searchContent:''
		}
	}
	componentDidMount(){
		let self =this;
		$.get("/components/getData",{},function(data){
			self.setState({
				data:data
			});
			self.paintTable();
	});


	}
	/**
	 * 
	 */
	paintTable(){
		let self = this;
		self.state.$datatable =$(".main-tb").DataTable({
		data:self.state.data,
		  retrieve:true,
		 "columns": [
        { "data": "_id" ,"title":"id"},
        { "data": "name","title":"" },
        { "data": "age","title":"" }
    	]
		});
		self.state.$datatable.draw();
	}
	queryDataTables(){
		let self = this;
		
		$.get("/components/getData",{name:self.state.searchContent},function(data){
			console.log(data);
			self.setState({
				data:data
			});
			self.state.$datatable.destroy();
			self.paintTable();
			
		});
	}
	updateUser(){
		let self = this;
		$.get("/components/updateData",{},function(data){
			alert(data);

		});
	}
	handleInput(event){
		let self = this;
		self.setState({
			searchContent:event.target.value
		});
	}
	render(){
		let self = this;
		return (
			<div className="dtable">
			<input type="text" value={self.state.searchContent} onChange={self.handleInput.bind(this)} className="search"/>
			<button onClick={self.queryDataTables.bind(this)}></button>
			<table className="main-tb">
			</table>

			<button onClick={self.updateUser.bind()}></button>
			</div>
			)
	}
}
 ReactDOM.render(<App/>,
 document.getElementById('app') );



